import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Header = ({ siteTitle }) => {
  const pages = [
    { link: '/', name: 'home' },
    { link: 'profile', name: 'profile' },
    { link: 'projects', name: 'projects' }, { link: 'contact', name: 'contact' },
    { link: 'skills', name: 'skills' }
  ]
  return (
    <>
      <nav className="flex items-center justify-between flex-wrap bg-white-500 p-6 border">
        <div className="flex items-center flex-shrink-0 mr-6">
          {pages.map((page, key) =>
            <Link to={page.link} key={key} className="lg:inline-block lg:mt-0 text-dark-500 hover:text-yellow mr-4">
              {page.name}
            </Link>
          )}
        </div>
        <div className="block lg:hidden">
          <button className="flex items-center px-3 py-2 border rounded text-teal-200 border-teal-400 hover:text-white hover:border-white">
            <svg className="fill-current h-3 w-3" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><title>Menu</title><path d="M0 3h20v2H0V3zm0 6h20v2H0V9zm0 6h20v2H0v-2z" /></svg>
          </button>
        </div>



      </nav>


    </>
  )
}



Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
